<template>
	<!--
		推荐人页面--填写推荐人信息+我自己的推荐码
		
	-->
	<view>
		<view class="content">
			<view>
				<view>我的推荐码：</view>
				<input disabled="true" v-model="userInfo.reserved2"/>
			</view>
			<view>
				<view>我的推荐人：</view>
				<input v-model="userInfo.reserved4" :disabled="edit" />
			</view>
			<view class="c-button" @tap="recommend">
				确定
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				userInfo:{},
				edit:false
			}
		},
		//页面显示时---获取用户相关信息
		onShow(){
			var userInfo = uni.getStorageSync("userInfoObj")
			if(!userInfo){
				uni.showToast({
					title:"请登录",
					icon:"none",
					duration:1000
				})
				setTimeout(()=>{
					uni.navigateTo({
						url:"/pages/login/login"
					})
				},1000)
				return;
			}
			this.userInfo = userInfo;
			if(userInfo.reserved4){
				this.edit = true
			} 
		},
		methods: {
			//开始推荐
			recommend(){
				if(this.edit){
					return;
				}
				var that = this;
				this.$api.recommend({
					id:that.userInfo.id,
					reserved4:that.userInfo.reserved4
				}).then(resp=>{
					if(resp==0){
						uni.showToast({
							title:"推荐码不正确",
							icon:"none"
						})
					}else{
						uni.setStorageSync("userInfoObj",that.userInfo)
						that.edit = true;
					}
				})
			}
		}
	}
</script>

